import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {GetUserCouponList} from 'api/user'
export default{
	components:{
		UniLoadMore
	},
	data(){
		return {
			page:1,
			pageCount:0,
			moreStatus:"",
			list:[]
		}
	},
	onLoad() {
		
	},
	onShow() {
		this.getList()
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			this.getList(this.page)			
		}	
	},
	methods:{
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			GetUserCouponList({
				page:this.page,
				pagesize:10,
			}).then(res=>{
				this.pageCount = res.pagecount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.list = this.list.concat(res.data)
				}
			})		
		}
	}
}
